.navbar{
    background:goldenrod;
    padding: 20px;
    color:#fff;
}
.navbar nav{
    display:flex;
    align-items: center;
    max-width: 1200px;
    margin:0 auto;
}
.navbar a.brand{
    color:#fff;
    margin-right: auto;
    text-decoration: none;
}
.navbar a:last-child{
    color:#fff;
    margin-left: 40px;
    padding: 8px;
    text-decoration: none;
}