@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
/* base styles */
html, body, #root {
  height: 100%;
}
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.4em;
  background: #dfdfdf;
  color: #333;
}
h1,h2,h3,p {
  margin: 0;
}
input, textarea {
  color: #333;
  padding: 8px;
  font-size: 1em;
  border-radius: 4px;
  border: 1px solid #d4d4d4;
  background-color: #fff;
  display: block;
  width: 100%;
  box-sizing: border-box;
}
.page-title {
  text-align: center;
  margin: 40px auto;
  color: #333;
}
.loading, .error {
  text-align: center;
  margin: 40px auto;
}
.navbar{
    background:goldenrod;
    padding: 20px;
    color:#fff;
}
.navbar nav{
    display:flex;
    align-items: center;
    max-width: 1200px;
    margin:0 auto;
}
.navbar a.brand{
    color:#fff;
    margin-right: auto;
    text-decoration: none;
}
.navbar a:last-child{
    color:#fff;
    margin-left: 40px;
    padding: 8px;
    text-decoration: none;
}
div{
    text-align: center;
    color:red;
}



.App {
  min-height: 100%;
}
